.product_detail {
    .container {
        .detail_right {
            .quantity {
                .number-quantity {
                    margin-bottom: 0;
                    display: flex;
                    align-items: center;
                }

                ;

                .btn {
                    i {
                        font-size: 0.8rem;
                    }
                }
            }
        }
    }
}

;

.products {
    padding-top: 2rem;
    background-color: #FFFCFC;
    .title {
        color: #ffffff;
        background: linear-gradient(180deg, #F21299 0%, #1B02B5 100%);
        padding-left: 2rem;

        span {
            font-size: 2.5rem;
        }

        ;
    }

    ;

    .container-fluid {
        .products_show {
            max-width: 1280px;

            .content {
                .item {
                    background: #F8F8F8;

                    /* shadow */
                    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
                    .card-top {
                        .card-img-top {
                            width: 100%;
                            margin: auto;
                        };
                        .fa-heart {
                            color: #ff0000;
                            position: absolute;
                            font-size: 1.8rem;
                            right: 1rem;
                            top: 1rem
                        }
                    }
                    

                    ;

                    .card-title {
                        margin: 0;
                        width: 100%;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }
;
                    .card-descript {
                        color: #CBC9C9;
                        width: 85%;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }
                    ;

                    .card_footer {
                        .button-buy {
                            font-size: 24px;
                            color: #000;
                            font-weight: 400;
                            border: none;
                            border-radius: 0;
                            padding: 0.8rem 1rem;
                            background-color: #9DE167;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;

                            &:hover {
                                background-color: #FF9800;
                                transition: 0.5s
                            }
                        }

                        ;

                        .price {
                            font-size: 1.5rem;
                            font-weight: 600;
                            background-color: #DEDDDC;
                            .price-text {
                                display: inline-block;
                                vertical-align: -webkit-baseline-middle;
                                text-align: center;
                                width: 100%;
                            }
                        }
                    }
                }
            }
        }
    }
}